module.exports = "## 同步操作\r\n### 下载项目\r\n 多人协作时，建议同一个人新建项目及API，其他人下载项目即可。步骤：\r\n - 在配置中填写服务器地址：http://192.168.xx.xx:6001\r\n - 点击同步页面，点下载即可\r\n\r\n### 下载API\r\n 点击项目下的打开，选中API，下载即可。\r\n\r\n## 说明\r\n### 分类说明\r\n  共分为6类。判断是否相同是根据该API的修改时间决定。每个API在创建的时候会生成一个独立的uid，所以不同人创建不必然不同。为同步需要，API创建是同一个人时同步才会生效。其他人建议用同步的方式复制。\r\n  - 落后于服务端\r\n  - 领先于服务端\r\n  - 本机独有\r\n  - 服务端独有\r\n  - 无变化项目\r\n  - 未知项目\r\n  \r\n";

